/* Global */

* {
    margin: 0;
    padding: 0;
}

body {
    font-size: 16px;
    background-color: rgb(250, 250, 250);
    color: rgb(60, 60, 60);
    font-family: monospace;
}

select  {
    border: solid rgb(226, 226, 226) 2px;
    font-size: inherit;
    border-radius: 5px;
}

button {
    font-family: monospace;
    font-size: 24px;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
}

button:focus {
    outline: none;
}

button:hover {
    cursor: pointer;
}

h1, .title {
    font-size: 48px;
}

h2, .subtitle {
    font-size: 32px;
}

h3, .subsubtitle {
    font-size: 24px;
}

button.act {
    display: block;
    font-size: 24px;
    border: none;
    border-radius: 8px;
    padding: 10px 20px;
    color: white;
    margin: 20px auto;
}

button.danger {
    background-color: #F0241A;
    color: white;
}

button.danger:hover {
    background-color: #c41d14;
}

button.safe {
    background-color: #28a745;
    color: white;
}

button.safe:hover {
    background-color: #24983f;
}

.hide {
    display: none !important;
}

.right {
    float: right;
}

/* Header */

.header {
    background-color: rgb(240, 240, 240);
    display: flex;
}

.header > .filler {
    width: 20%;
    min-width: 20%;
    flex: 0 0 0;
    background-color: white;
    text-align: center;
    font-size: 48px;
    line-height: 68px;
    border-right: solid rgb(226, 226, 226) 3px;
    box-sizing: border-box;
}

.header > button {
    padding: 20px;
    flex: 1 1 0;
    text-align: center;
    color: inherit;
    text-decoration: none;
    border: none;
    border-radius: 0;
}

.header > button:hover {
    background-color: rgb(226, 226, 226);
}

.header > button.active {
    color: rgb(250, 250, 250);
    background-color: #0388FC;
}

.header > button.active:hover {
    background-color: #0281F0;
}

/* Project Iteration Selector Styling */


.project-iteration-selector {
    float: right;
    padding: 4px;
    border: solid 2px rgb(226, 226, 226);
    border-radius: 5px;
    background-color: rgb(240, 240, 240);
}

.project-iteration-description {
    padding: 0 5px 5px;
    border-bottom: solid 2px rgb(226, 226, 226);
    margin-bottom: 5px
}

.project-selector {
    width: 73%;
}

.separator {
    display: inline-block;
    width: 5%;
}

.iteration-selector {
    width: 22%;
}

/* Table Styling */

.table-container {
    padding: 10px;
    border: solid rgb(226, 226, 226) 2px;
    margin: 10px 0;
    border-radius: 5px;
}

.table-description > p {
    margin: 10px 0;
}

.table-container table {
    width: 100%;
}

.table-container th {
    background-color: rgb(240, 240, 240);
}

.table-container thead th {

}

.table-container table, .table-container th, .table-container td {
    border: solid rgb(200, 200, 200) 1px;
    border-collapse: collapse;
    padding: 4px 3px;
}

/* Tab Styling */
.tab-content {
    display: flex;
    width: 100%;
    height: calc(100vh - 110px);
    overflow: hidden;
}

.tab-header {
    margin-bottom: 10px;
}

.flex-actions {
    display: flex;
    justify-content: space-evenly;
}

ul.projects {
    list-style-type: none;
}

ul.projects > li {
    margin: 10px;
    display: flex;
    justify-content: space-between;
    font-size: inherit;
}

ul.projects > li > input {
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 5px;
    border-width: 1px;
}

ul.projects > li > input:disabled {
    border-color: transparent;
}

.sidebar {
    max-width: 20%;
    min-width: 20%;
    width: 20%;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    border-top: solid rgb(226, 226, 226) 3px;
}

.sidebar > .action {
    box-sizing: border-box;
    width: 100%;
    font-size: 24px;
    color: inherit;
    padding: 15px 10px;
    border-left: none;
    border-top: none;
    border-right: solid rgb(226, 226, 226) 3px;
    border-bottom: solid rgb(226, 226, 226) 3px;
    border-radius: 0;
}

.sidebar-filler {
    flex: 1 1 auto;
    background-color: rgb(239, 239, 239);
    border-right: solid rgb(226, 226, 226) 3px;
}

.sidebar > .action:hover {
    background-color: rgb(226, 226, 226);
}

.sidebar > .action.active {
    border-right: solid rgb(250, 250, 250) 3px;
    background-color: rgb(250, 250, 250);
}

.output-container {
    padding: 20px;
    width: 80%;
    border-top: solid rgb(226, 226, 226) 3px;
}

.output {
    margin: 0 auto;
    width: 100%;
    max-width: 700px;
    overflow-y: scroll;
}

/* Status Styling */

.status-container {
    padding: 10px;
    border: solid rgb(226, 226, 226) 2px;
    margin: 10px 0;
    border-radius: 5px;
}

.status {
    margin: 10px 0;
}

/* Attribution */

.footer {
    padding: 10px;
    background-color: rgb(240, 240, 240);
    border-top: solid rgb(226, 226, 226) 3px;
}

.attribution {
    text-align: center;
}

/* Scrollbar */

::-webkit-scrollbar {
    width: 0;
}


